import axios from 'axios';
import { JwtTokenService } from './token';
import { HttpOptions, RequestOptions, HttpServiceInterface } from './interface';

export class HttpService implements HttpServiceInterface {
  readonly #tokenService: JwtTokenService;

  readonly #options: HttpOptions;

  constructor(tokenService: JwtTokenService, options: HttpOptions) {
    this.#tokenService = tokenService;
    this.#options = options;
  }

  public async get<T>(
    url: string,
    options: RequestOptions = {},
    queryParams: Record<string, string | number | boolean> = {},
  ): Promise<T> {
    const baseUrl = options.baseUrl || this.#options.baseUrl;
    const token = await this.#tokenService.getToken({
      ...this.#options,
      ...options,
    });

    const { data } = await axios.get<T>(`${baseUrl}${url}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      params: queryParams,
    });

    return data;
  }

  public async post<T>(
    url: string,
    body: string | object,
    options: RequestOptions = {},
  ): Promise<T> {
    const baseUrl = options.baseUrl || this.#options.baseUrl;
    const token = await this.#tokenService.getToken({
      ...this.#options,
      ...options,
    });

    const { data } = await axios.post<T>(`${baseUrl}${url}`, body, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });

    return data;
  }

  public async put<T>(
    url: string,
    body: string | object,
    options: RequestOptions = {},
  ): Promise<T> {
    const baseUrl = options.baseUrl || this.#options.baseUrl;
    const token = await this.#tokenService.getToken({
      ...this.#options,
      ...options,
    });

    const { data } = await axios.put<T>(`${baseUrl}${url}`, body, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });

    return data;
  }
}
